.card {
  position: relative;
  background-color: transparent;
  padding-top: 100px;
  margin-bottom: 100px;

  /* margin-top: 20px; */
}

.card::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(
    to top,
    rgba(255, 255, 255, 0.151),
    rgba(255, 255, 255, 0)
  );
}

.card-content {
  z-index: 100;
  position: relative;
  background-color: transparent;
  padding: 100px;
  /* margin-top: 20px; */
  /* height: 800px; */
}

.sec-moon,
.sec-sun,
.sec-mountain-1 {
  transform: scale(0.5);
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.sec-sea {
  /* display: inline-flex;
    align-self: flex-end; */
  position: relative;
  /* background-color: rgb(0, 141, 184); */
  width: 100%;
  height: 60vh;
  z-index: 9;
  overflow: hidden;
  bottom: 350px;
  background: radial-gradient(
    farthest-corner at 50% -100%,
    rgb(65, 211, 255) 40%,
    rgb(0, 126, 165) 75%,
    rgb(0, 100, 131)
  );
}

.sec-sun {
  position: relative;
  z-index: 8;
}

.sec-mountain-1 {
  bottom: -150px;
  z-index: 10;
  position: relative;
}

