@keyframes spring {
  0% {
    bottom: -800px;
  }
  25% {
    bottom: 0px;
  }
  100% {
    bottom: 0px;
  }
}

.moon {
  -webkit-animation: spring 5s cubic-bezier(0.7, 1.5, 0.7, 1);
  animation: spring 5s cubic-bezier(0.7, 1.5, 0.7, 1);
}

.sun {
  -webkit-animation: spring 3s cubic-bezier(0.7, 1.5, 0.7, 1);
  animation: spring 3s cubic-bezier(0.7, 1.5, 0.7, 1);
}
/* start moon */
.moon {
  background-color: #ffffff;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  position: relative;
  box-shadow: inset 30px -5px #f0f0f0, inset 15px 0px 1px 1px #f8f8f8;
}

.moon::before {
  content: "";
  position: absolute;
  -webkit-filter: blur(10px);
  filter: blur(10px);
  border: 20px solid;
  border-color: white;
  border-radius: 50%;
  height: 130px;
  width: 130px;
  margin-left: -86px;
  margin-top: -9px;
  animation: lightmoon 2s infinite;
  -webkit-animation: lightmoon 2s infinite;
}

.moon .dmoon-0,
.moon .dmoon-1,
.moon .dmoon-2,
.moon .dmoon-3,
.moon .dmoon-4,
.moon .dmoon-5,
.moon .dmoon {
  position: absolute;
}

.dmoon-0 {
  top: 50px;
  left: 20px;
  height: 15px;
  width: 15px;
}

.dmoon-1 {
  top: 125px;
  left: 95px;
  height: 8px;
  width: 8px;
}

.dmoon-2 {
  top: 25px;
  left: 55px;
  height: 10px;
  width: 10px;
}

.dmoon-3 {
  top: 65px;
  left: 115px;
  height: 10px;
  width: 10px;
}

.dmoon-4 {
  top: 115px;
  left: 50px;
  height: 12px;
  width: 12px;
}

.dmoon-5 {
  top: 35px;
  left: 105px;
  height: 20px;
  width: 20px;
}

.dmoon {
  border-radius: 50%;
  border: 1px solid;
  border-color: #dfdfdf;
  box-shadow: inset -2px -2px #dfdfdf;
  /* -moz-box-shadow: inset 0px 16px #999, inset 0px 16px 1px 1px #999; */
}

@keyframes lightmoon {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    border-color: white;
  }
  100% {
    opacity: 0;
  }
}
/* end moon */

