/* start wave */
@media (prefers-reduced-motion: no-preference) {
  .wave {
    position: relative;
    width: 50px;
    height: 10px;
    border-radius: 50%;
    /* background: white; */
    box-shadow: inset 0 -2px 3px -2px #fff;
    -webkit-animation: waveFade 2s ease infinite;
    animation: waveFade 2s ease infinite;
  }

  .wave::before {
    content: "";
    position: absolute;
    width: 50px;
    height: 10px;
    border-radius: 50%;
    top: -10px;
    left: -50px;
    /* background: white; */
    box-shadow: inset 0 -2px 3px -2px #fff;
    -webkit-animation: waveFade 2s ease infinite;
    animation: waveFade 2s ease infinite;
  }

  .wave::after {
    content: "";
    position: absolute;
    width: 50px;
    height: 10px;
    top: 0px;
    left: 40px;
    border-radius: 50%;
    /* background: white; */
    box-shadow: inset 0 -2px 3px -2px #fff;
    -webkit-animation: waveFade 2s ease infinite;
    animation: waveFade 2s ease infinite;
  }
}
@keyframes waveFade {
  0% {
    opacity: 0;
    margin-top: 10px;
  }
  50% {
    opacity: 1;
    margin-top: 20px;
  }
  100% {
    opacity: 0;
    margin-top: 30px;
  }
}
/* end wave */

/* start cloud */

.cloud-1 {
  bottom: 800px;
  z-index: 30;
  -webkit-animation: cloudFade 50s linear reverse infinite;
  animation: cloudFade 50s linear reverse infinite;
  transform: scale(0.5);
}

.cloud-2 {
  bottom: 700px;
  z-index: 31;
  -webkit-animation: cloudFade 30s linear infinite;
  animation: cloudFade 30s linear infinite;
  transform: scale(0.6);
}

@keyframes cloudFade {
  0% {
    opacity: 0;
    right: 0em;
  }
  25% {
    /* left: 0em; */
    right: 12.5em;
    opacity: 0.8;
  }
  50% {
    /* left: 0em; */
    right: 25em;
    opacity: 0.8;
  }
  75% {
    /* left: 0em; */
    right: 37.5em;
    opacity: 0.8;
  }
  100% {
    right: 50em;
    opacity: 0;
  }
}

.cloud {
  display: flex;
  position: absolute;
  background: #fff;
  width: 250px;
  height: 100px;
  border-radius: 50%;
}

.cloud::before {
  content: "";
  position: absolute;
  background: #fff;
  width: 300px;
  height: 100px;
  bottom: -40px;
  left: 50px;
  border-radius: 50%;
  box-shadow: inset -1px -5px 0 rgb(195, 195, 195);
}

.cloud::after {
  content: "";
  position: absolute;
  background: #fff;
  width: 300px;
  height: 100px;
  bottom: -40px;
  left: -100px;
  border-radius: 50%;
  box-shadow: inset 1px -5px 0 rgb(195, 195, 195);
}

@media (prefers-reduced-motion: no-preference) {
  .star {
    position: absolute;
    /* background: #fff; */
    width: 2px;
    height: 2px;
    border-radius: 50%;
    box-shadow: 10em 15em 0 #fff, -8em 10em 0 #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 0 #fff, 8em 15em 0 #fff, 16em 12em 0 #fff,
      -11em 19em 0 #fff;
    -webkit-animation: star 10s infinite linear;
    animation: star 10s infinite linear;
  }

  .meteor {
    position: absolute;
    background: linear-gradient(
      to right,
      rgb(255, 190, 70),
      rgb(176, 176, 255)
    );
    transform: rotateZ(45deg);
    width: 2px;
    height: 2px;
    left: 40%;
    top: 25%;
    border-radius: 50%;
    -webkit-animation: meteor 10s linear infinite;
    animation: meteor 10s linear infinite;
  }
}

@keyframes star {
  0% {
    box-shadow: 10em 15em 1px 2px #fff, -8em 10em 0 #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 0 #fff, 8em 25em 0 #fff, 16em 12em 0 #fff,
      -11em 19em 0 #fff;
  }
  12.5% {
    box-shadow: 10em 15em 0 #fff, -8em 10em 0 #fff, -15em 16em 1px 2px #fff,
      0em 19em 0 #fff, 12em 26em 1px 2px #fff, 8em 25em 0 #fff, 16em 12em 0 #fff,
      -11em 19em 1px 2px #fff;
  }
  25% {
    box-shadow: 10em 15em 1px 2px #fff, -8em 10em 0 #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 0 #fff, 8em 25em 1px 2px #fff, 16em 12em 0 #fff,
      -11em 19em 0 #fff;
  }
  37.5% {
    box-shadow: 10em 15em 0 #fff, -8em 10em 0 #fff, -15em 16em 0 #fff,
      0em 19em 1px 2px #fff, 12em 26em 0 #fff, 8em 25em 0 #fff, 16em 12em 0 #fff,
      -11em 19em 0 #fff;
  }
  75% {
    box-shadow: 10em 15em 0 #fff, -8em 10em 1px 2px #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 0 #fff, 8em 25em 0 #fff, 16em 12em 0 #fff,
      -11em 19em 1px 2px #fff;
  }
  87.5% {
    box-shadow: 10em 15em 0 #fff, -8em 10em 0 #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 0 #fff, 8em 25em 0 #fff, 16em 12em 1px 2px #fff,
      -11em 19em 0 #fff;
  }
  100% {
    box-shadow: 10em 15em 0 #fff, -8em 10em 1px 2px #fff, -15em 16em 0 #fff,
      0em 19em 0 #fff, 12em 26em 1px 2px #fff, 8em 25em 0 #fff, 16em 12em 0 #fff,
