@media screen and (max-width: 800px) and (max-height: 1100px) {
  .sec-sun,
  .sec-moon {
    transform: scale(0.3);
  }
  .meteor {
    left: -10% !important;
  }
  .meteor,
  .star,
  .wave {
    transform: scale(0.4);
    margin-top: 30%;
  }
  .sec-mountain-1 {
    left: 20px;
    transform: scale(0.17);
  }
  .sec-moon {
    margin-top: 5em;
  }
  .sec-mountain-1 {
    bottom: 150px;
  }
  .sec-sea {
    /* height: 20%; */
    position: absolute;
    top: 470px;
  }
  .btn {
    transform: scale(0.7);
  }
  div.col div {
    display: unset !important;
  }
  p {
    margin-left: -60px;
    margin-right: -60px;
    padding-left: 0;
    padding-right: 0;
  }
  p.col-1,
  p.col-2,
  p.col-3,
  p.col-4 {
    width: fit-content !important;
  }
  p.center {
    display: flex !important;
    flex-direction: column !important;
  }
}
/** mobile: end  */

@media screen and (max-height: 600px) {
  .sec-sun,
  .sec-moon,
  .sec-mountain-1 {
    transform: scale(0.3);
  }
  .card {
    height: 500px;
  }
  .meteor,
  .star,
  .wave {
    transform: scale(0.5);
    margin-top: -200;
  }
  .sec-moon {
    margin-top: -5em;
  }
  .sec-mountain-1 {
    transform: scale(0.17);
    bottom: 200px;
  }
  .sec-sea {
    position: absolute;
    top: 270px;
    height: 100vh;
  }
  .btn {
    transform: scale(0.7);
  }
}
/* *screen: end */

.body {
  position: relative;
  text-align: center;
  justify-content: center;
  /* background: #015077; */
}

.nav-bar {
  display: flex;
  position: absolute;
  /* padding: 20px; */
  width: 100%;
  height: 10%;
  z-index: 1000;
  justify-content: center;
  /* align-content: center; */
}

.footer {
  display: flex;
  z-index: 1000;
  position: relative;
}

.row-1 {
  height: 25vh;
}

.row-2 {
  height: 50vh;
}

.row-3 {
  height: 75vh;
}

.row,
.row-4 {
  height: 100vh;
}

.row-5 {
  height: 125vh;
}

.row-6 {
  height: 150vh;
}

.col,
.col-4 {
  width: 100%;
}

.col-1 {
  width: 25%;
}

.col-2 {
  width: 50%;
}

.col-3 {
  width: 75%;
}

.left {
  /* display: flex; */
  justify-content: flex-start !important;
  justify-self: flex-start !important;
  justify-items: flex-start !important;
  text-align: left !important;
  align-self: center !important;
}
div.col div {
  display: flex;
}
.center {
  /* display: flex; */
  justify-content: center !important;
  justify-self: center !important;
  justify-items: center !important;
  text-align: center !important;
  align-self: center;
  align-items: center;
}

.right {
  /* display: flex; */
  justify-content: flex-end !important;
  justify-self: flex-end !important;
  justify-items: flex-end !important;
  text-align: right !important;
  align-self: center;
}

.start {
  align-self: flex-start !important;
  align-items: flex-start !important;
}

.end {
  align-self: flex-end !important;
  align-items: flex-end !important;
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.app {
  text-align: center;
}

.small {
  top: 5px;
  left: 5px;
  color: white;
  font-size: small;
}
.medium {
  top: 5px;
  left: 5px;
  color: white;
  font-size: large;
}
.large {
  top: 5px;
  left: 5px;
  color: white;
  font-size: xx-large;
}

.btn {
  display: inline-flex;
  justify-content: center;
  position: relative;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 4px;
  background: transparent;
  color: white;
  border-radius: 10px;
  border: 2px solid transparent;
  font-family: "Baloo 2", cursive;
  outline: none; /* disable box button*/
}

.btn-bloat:active {
  box-shadow: 0px 3px 5px #ffffff1f, 0px -3px 5px #00000067;
}

.btn-bloat {
  border: 1px solid transparent;
  box-shadow: 0px -5px 5px #ffffff1f, 0px 5px 5px #00000067;
}

.btn-float {
  box-shadow: 0px 15px 15px #00000067;
}

.is-active {
  box-shadow: 0px 3px 5px #ffffff1f, 0px -3px 5px #00000067;
}

.round {
  border-radius: 34px;
}

.round::before {
  border-radius: 50%;
}
.text-default {
  color: #2196f3;
}
.text-dark {
  color: #1e252a;
}
.text-light {
  color: whitesmoke;
}
.default {
  background-color: #2196f3;
}
.dark {
  background-color: #1e252a;
}
.light {
  background-color: whitesmoke;
  color: #1e252a;
}
.box-border {
  border: 2px solid #1e252a;
  background-color: transparent;
}
/** animation  */
.slideUp {
  animation: slideUp 1s;
  -webkit-animation: slideUp 1s;
  visibility: hidden;
}

@keyframes slideUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(70%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
  }
}

@-webkit-keyframes slideUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(70%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
  }
}
/** animation  */

